@import url(https://fonts.googleapis.com/css2?family=Lilita+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.select-files-container {
  margin-bottom: 40px;
}

.Trow { 
  border-bottom: 1px solid #D79922;
  text-align: left;
  margin: 5px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: #C5CCE3;
}

.content {
  padding: 10px;
}

.clear-button {
  background-color: #4056A1;
  color: #FDFEFF;
  font-weight: bold;
  text-decoration: none;
  margin-top:10px;
  margin-bottom:10px;
}

.clear-button:hover {
  color: #FDFEFF;
  background-color: #2c469c;
  font-weight: bold;
  text-decoration: none;
}

.hr {
    color: #D79922;
    background-color: #D79922;
    size: 1;
    width: 90%;
    margin-top: 20px;
}
.bodyPage {
    background-color: #F1F0EB;
    min-height:100vh;
    width: 100vw;
    padding-bottom:10%;
}

.mainRow {
    background-color: #F1F0EB;
}

.download-button {
    border: 1px solid #4056A1;
    border-radius: 5px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #4056A1;
    color: #F1F0EB;
    font-weight: bold;
    margin-bottom: 5%;
}

.download-button:hover {
    color: #F1F0EB;
    background-color: #2c469c;
    font-weight: bold;
    text-decoration: none;
}

.disabled-download {
    border: 1px solid grey;
    border-radius: 5px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 10px;
    background-color: grey;
    color: black;
    font-weight: bold;
    margin-bottom: 5%;
    width: 100px;
}

.submitB {
    background-color: #4056A1;
    color: #F1F0EB;
    font-weight: bold;
    text-decoration: none;
    margin-top:10px;
    margin-bottom:15px;
}

@media (max-width: 550px) {
    .submitB {
        margin-top: 15px;
    }

    .bodyPage {
        background-color: #f7f4ea;
        /* height: 175vh; */
    }
 }
  
.submitB:hover {
    color: #FDFEFF;
    background-color: #2c469c;
    font-weight: bold;
    text-decoration: none;
}

.display-container {
    margin-left: auto;
    margin-right: auto;
    margin-top: 2%;
    margin-bottom: 3%;
    display:block;
    padding: 2%;
    background-color: black;
}
.navbar-brand {
    font-family: 'Lilita One';
    font-size: calc(50px + 2vmin);
    color:#D79922;
}

.nav-link {
    font-family: 'Lilita One';
    font-size: calc(15px + 2vmin);
    color: #D79922;
    margin-left: 10px;
}

.navbar-brand:hover {
    font-family: 'Lilita One';
    color:#D79922;
}

.nav-link:hover {
    font-family: 'Lilita One';
    color: #D79922;
}

.navigation {
    background-color: #4056A1;
}
.footer {
    background-color: #4056A1;
    height: 70px;
    width: 100%;
    text-align: center;
}

.copy {
    padding-top: 22px;
    color:#D79922;
  }
.purposePage {
    background-color: #F1F0EB;
    width: 100vw;
}

.explain {
    font-family: 'Lilita One';
    font-size: calc(15px + 2vmin);
    color: #D79922;
    margin-left: 10px;
    padding-top: 5%;
    text-align: center;
}

.r1 {
    font-family: 'Lilita One';
    font-size: calc(10px + 2vmin);
    color: #D79922;
    margin-left: 10px;
    padding-top: 3%;
    text-align:center;
}

.specPage {
    background-color: #F1F0EB;
    width: 100vw;
    min-height:100vh;
    width: 100vw;
    padding-bottom:10%;
}

.define-random {
    font-family: 'Lilita One';
    font-size: calc(8px + 2vmin);
    color: #D79922;
    margin-left: 10px;
    padding-top: 5%;
    text-align: center;
}
